import { useEffect, useState } from "react"
import { FiltersType } from "../../BLL/Reducers/rimsReducer"

type PropsType={
    page:number
    totalCount:number
    filters:FiltersType
    setPage:(filters:FiltersType,page:number)=>void
}

let Pagination:React.FC<PropsType>=(props)=>{
    useEffect(()=>{},[props.page,props.totalCount])
    //Portion size 9
    let countButtons=Math.ceil(props.totalCount/9)
    let portionNumber=0
    
    //To understand where our current button(number portion)
    while (portionNumber*5<props.page && props.page<(portionNumber+1)*5){portionNumber++}
    
    let [curPortionNumber,setPortionNumber]=useState(portionNumber)
    let onNext=()=>{
        setButtons(createButtons(curPortionNumber+1,props.page,countButtons))
        setPortionNumber(curPortionNumber+1)
    } 
    let onPrev=()=>{
        setButtons(createButtons(curPortionNumber-1,props.page,countButtons))    
        setPortionNumber(curPortionNumber-1)
    }



    let createButtons=(i:number,page:number,countButtonsForFunc:number)=>{
    let buttons=[]
    for(let a=((i-1)*5)+1;a<=(i)*5 && a<=countButtonsForFunc;a++){
        buttons.push(
            <button onClick={()=>{props.setPage(props.filters,a)}}
            className={page==a ? 
            "btn btn-light" :"btn btn-primary"} >
                {a}
            </button>
        )
        }
        return buttons
    }
    let [curButtons,setButtons]=useState(createButtons(curPortionNumber,props.page,countButtons))
    return<div>

        {curPortionNumber>1  &&  
        <button className="btn btn-outline-primary" onClick={onPrev}>
            {"<"}
        </button>}

        {curButtons}

        {countButtons+5>=(curPortionNumber+1)*5 &&   
        <button className="btn btn-outline-primary" onClick={onNext}>
            {">"}
        </button>}
    </div>
}

export default Pagination